import { _decorator, Component, Node, Label, director, sys } from 'cc';
const { ccclass, property } = _decorator;

import { AudioManager } from "../common/AudioManager";

import { Constant } from "../common/Constant";

@ccclass('Home')
export class Home extends Component {




    @property(Label)
    lblFullName: Label = null;
  
  
    @property(Label)
    lblDateOfBirth: Label = null;

    @property(Node)
    imgFemale: Node = null;
  
    @property(Node)
    imgMale: Node = null;


    onLoad(){
        this.initLayout()
    }


    initLayout() {
 var dataUser = JSON.parse(sys.localStorage.getItem(Constant.PERSONAL));
// ////console.log("Profile......");
    if (dataUser == undefined || dataUser == null) {
// //return;
    } else {

    this.lblFullName.string = dataUser.fullName;
    this.lblDateOfBirth.string=`${parseInt(dataUser.dayOfBirth)}/${parseInt(dataUser.monthOfBirth)}/${parseInt(dataUser.yearOfBirth)}`

    if (dataUser.sex == 0) {
       this.imgFemale.active = false;
       this.imgMale.active = true;
    } else {
      this.imgFemale.active = true;
      this.imgMale.active = false;
    }
    }    }


    start() {


    }

    update(deltaTime: number) {
        
    }



    loadProfile(){
        director.loadScene("Profile");

    }

    loadSettings() {
        director.loadScene("Settings");
    }
}

